import { Component, OnInit } from '@angular/core';
import {BusSchedule} from '../busSchedule';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})

export class MainComponent implements OnInit {
  car : string = "Car";
  message : string = "30 minute commute by car";
  array : string[] = ["Car", "Bus"];
  schedule : BusSchedule[] = [{
    route: 1, timeLeave: "19:00", timeArrive: "18:00"
  }, {
    route: 2, timeLeave: "20:00", timeArrive: "19:00"
  }];

  constructor() { }

  ngOnInit() {
  }

  onInput(event: any){
    if(this.car == "Car"){
      this.message = "30 minute commute by car";
    } else {
      this.message = "Bus and Train-1 hour commute";
    }
  }

  onClick(i: any){
    if(i == 0){
      this.car = "Car";
    } else if(i==1){
      this.car = "Bus";
    }
    this.onInput(i);
  }

}
